
label {
  font-size: 13px;
  color: #141414;
}

input, select {
  padding: 10px 8px;
  font-size: 18px;
  border: 1px #D6D6D6 solid;
}

select {
  height: 40px;
  width: 97%;
  margin: 0 0 16px 0;
  display: block;
  border-radius: 0;
  -webkit-appearance: none;
  -webkit-border-radius: 0px;
}

input[type="search"] {
  -webkit-appearance: textfield;
}

input.btn-wide {
  width: 100%;
  cursor: pointer;
}

textarea {
  display: block;
  width: 95%;
  font-size: 14px;
  padding: 5px;
}

button, input[type=submit],
.button, .gem-polls a,
.search__submit {
  display: inline-block;
  background: #ed008c;
  font-family: 'TwCenMT';
  color: #fff;
  border: 0;
  font-size: 12px;
  font-weight: bold;

  height: auto;
  line-height: 1.3333;
  padding: 8px 10px;

    margin-bottom: 15px;
    text-transform: uppercase;
  @media screen and (min-width:480px) {
    padding: 10px 24px;
    font-size: 18px;
  }
}


.error {
  color: red;
}

.input-error {
  border: solid 1px red;
}

.label-wide, .input-wide {
  display: block;
  width: 100%;
  margin-bottom: 1em;
}

#id_terms_and_conditions {
  display: inline;
  width: auto;
  margin: 0 10px 0;
  vertical-align: middle;
}
